import './_vendor';
import vars from './_vars';
import './_functions';
import './_components';

// pc pagination
$('.intro__pagination li').click(function(){
    $('.intro__pagination li').removeClass('active');
    $(this).addClass('active');
});

// sticky header and pagination 
$(window).scroll(function(){
    var scrollPos = $(document).scrollTop();
    if(scrollPos > 100){
        $('.header').addClass('scrolled');
        $('.intro__pagination').addClass('scrolled');
    } else{
        $('.header').removeClass('scrolled');
        $('.intro__pagination').removeClass('scrolled');
    }
});

// mobile pagination
var number = 0;
// previous btn
$('.prev').click(function(e){
    if(number > 0){
        number--;
        $('.intro__pagination a')[number].click();
        e.preventDefault();
    }
});
// next btn
$('.next').click(function(e){
    if(number <  4){
        number++;
        $('.intro__pagination a')[number].click();
        e.preventDefault();
    }

});

// smooth scroll
document.querySelectorAll('a[href^="#"]').forEach(anchor => {
    anchor.addEventListener('click', function (e) {
        e.preventDefault();
        document.querySelector(this.getAttribute('href')).scrollIntoView({
            behavior: 'smooth'
        });
    });
});



// range input min values
function min(th){
    $(th).parent('.range-body').find('.min').html($(th).attr('min'));
}
// range input max values 
function max(th){
    $(th).parent('.range-body').find('.max').html($(th).attr('max'));
}

// min
min('#first-range');
min('#second-range');
// max
max('#first-range');
max('#second-range');

// range input show value position on input range slider change function
// watching and action function
function wathInputs(th){
    $(th).parent().find('.current').html($(th).val());
    var input = $(th).val() / $(th).attr('max');
    var width = $(th).css("width").replace('px', '');
    var value =  (input * width - ($(th).val() / 6)) + "px";
    $(th).parent().find('.current').css("left", value);
}

$(document).on('input', '.range', function() {
    // call a function when range input value has been changed
    wathInputs($(this));
});

$( document ).ready(function() {
    // call a function when document ready
    // first range watch at document ready function
    wathInputs('#first-range');
    // second range watch at document ready function
    wathInputs('#second-range');
});

// select
$('.calc-block__select').select2({ 
    minimumResultsForSearch : Infinity,
});
// reset for btn
$('.reset input').click(function(){
     // first range watch at document ready function
    rangeReset('#first-range');
    rangeReset('#second-range');
    $('.calc-block__select').select2({ 
        minimumResultsForSearch : Infinity,
    });
})

function rangeReset(th){
    $(th).parent().find('.current').html($(th).attr('value'));
    var input = $(th).attr('value') / $(th).attr('max');
    var width = $(th).css("width").replace('px', '');
    var value =  (input * width - ($(th).attr('value') / 6)) + "px";
    $(th).parent().find('.current').css("left", value);
}

